[data-theme='1'] {
  --color: #000000;
  --background: #ffffff;
}

[data-theme='2'] {
  --color: #000000;
  --background: #ddeeff;
}

[data-theme='3'] {
  --color: #ffffff;
  --background: #212a34;
}

html {
  background-color: var(--background);
  color: var(--color);
}

.select {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.07px;
  margin-top: 6px;
  padding:  0 10px;
  width: 171px;
  height: 36px;
}
