.container {
  width: 226px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  span:first-child {
    padding-right: 20px;
  }
  span:nth-child(3) {
    padding-left: 20px;
  }
  &:first-child {
    width: 100%;
    margin-top: 63px;
  }
  &:nth-child(2) {
    width: 100%;
    margin-top: 43px;
  }
}

